export default function (response) {
  const status = response.status || -10;
  let errorInfo = '';
  switch (status) {
    case -1:
      errorInfo = ',';
      break;
    case 400:
      errorInfo = '400';
      break;
    case 401:
      errorInfo = '401';
      break;
    case 403:
      errorInfo = '403';
      break;
    case 404:
      errorInfo = '404';
      break;
    case 405:
      errorInfo = '405';
      break;
    case 408:
      errorInfo = '408';
      break;
    case 500:
      errorInfo = '500';
      break;
    case 501:
      errorInfo = '501';
      break;
    case 502:
      errorInfo = '502';
      break;
    case 503:
      errorInfo = '503';
      break;
    default:
      errorInfo = '';
  }
  return {
    status,
    errorInfo,
  };
}
